@font-face {
  font-family: "Floripa";
  src: local("Floripa"), url("../fonts/floripa.woff") format("woff");
}
@font-face {
  font-family: "Gropled";
  src: local("Gropled"), url("../fonts/gropled.woff") format("woff");
}
html {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  margin: 0;
  width: 100%;
  background-color: #000;
  color: #fff;
}

.container {
  width: 100%;
  max-width: 768px;
  min-width: 375px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.title {
  margin: 0;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  text-transform: uppercase;
}

.text {
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 13px;
  text-align: center;
}

.btn {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  border: none;
  background: none;
  padding: 12px;
  padding-top: 12px;
  border: 1px solid #b9b9b9;
  border-radius: 18px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 1px;
  color: #ffffff;
  cursor: pointer;
  box-sizing: border-box;
}
.btn:hover {
  background-color: #fff;
  color: #000;
}
.btn--arrow {
  margin: 0;
  padding-right: 57px;
  position: relative;
}
.btn--arrow::after {
  position: absolute;
  content: "";
  width: 33px;
  height: 8px;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: center center;
}
.btn--arrow:hover {
  background-color: #fff;
  color: #000;
}

.btn--arrow:hover::after {
  background-image: url("../img/arrow-black.svg");
}

.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}
.page-header__wrapper {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  z-index: 5;
  background-color: #000;
  padding: 20px;
  padding-top: 40px;
  box-sizing: border-box;
  transform: translateY(-1000px);
  opacity: 0;
  transition: all 0.5s ease;
}
.page-header__wrapper--open {
  top: 70px;
  transform: none;
  opacity: 1;
}
.page-header__wrapper::after {
  position: absolute;
  content: "";
  width: 465px;
  height: 465px;
  background-image: url("../img/bg_agency.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  top: 50%;
  right: -50%;
  transform: translateY(-50%) rotate(55deg);
  z-index: -1;
}
.page-header__wrapper::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 64px;
  background-image: url("../img/bg_menu.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  left: 0;
  bottom: -63px;
}
.page-header__nav-link {
  padding-left: 13px;
  margin-bottom: 16px;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.page-header__nav-item:nth-of-type(1) .page-header__nav-link, .page-header__nav-item:nth-of-type(2) .page-header__nav-link, .page-header__nav-item:nth-of-type(3) .page-header__nav-link {
  margin-bottom: 20px;
  padding-left: 0;
  font-size: 24px;
  line-height: 24px;
}
.page-header__nav-toggle {
  position: relative;
  border: none;
  background: none;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 106%;
  color: #fff;
  min-width: 87px;
  min-height: 40px;
  text-align: left;
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.page-header__nav-toggle::after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  background-image: url("../img/icon-menu-open.svg");
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.page-header__nav-toggle--close::after {
  background-image: url("../img/icon-menu-close.svg");
}

.agency {
  position: relative;
  width: 100%;
  padding: 20px;
  margin-bottom: 60px;
  min-height: 120px;
  box-sizing: border-box;
}
.agency::after {
  content: "";
  position: absolute;
  display: block;
  width: 178px;
  height: 178px;
  background-repeat: no-repeat;
  background-size: 178px;
  background-position: center center;
  z-index: -1;
  background-image: url("../img/bg_agency.png");
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.agency__title {
  position: absolute;
  display: block;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-transform: uppercase;
  z-index: -3;
}
.agency__title:nth-of-type(2) {
  top: 90px;
  right: 20px;
  z-index: 1;
}

.about-us {
  padding: 0 20px;
  margin-bottom: 80px;
  background-image: url("../img/bg_about-us.png");
  background-size: 280px;
  background-position: center top -95px;
  background-repeat: no-repeat;
}
.about-us__title {
  text-align: center;
}

.webapp {
  padding: 0 20px;
  padding-top: 30px;
  margin-bottom: 60px;
  background-image: url("../img/bg_webapp.png");
  background-size: 400px;
  background-repeat: no-repeat;
  background-position: center top -120px;
}
.webapp__item {
  position: relative;
}
.webapp__item-title {
  margin: 0;
  text-transform: uppercase;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 33px;
  line-height: 33px;
  text-align: center;
  color: rgba(96, 96, 96, 0.5);
}
.webapp__item-text {
  margin: 0;
  margin-bottom: 8px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  text-align: right;
  color: #dddddd;
}
.webapp__item:nth-child(even) .webapp__item-text {
  text-align: left;
}
.webapp__list {
  margin-bottom: 25px;
}

.title-text {
  margin: 0;
  margin-bottom: 12px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  color: #949494;
}

.stages {
  padding: 0 20px;
  margin-bottom: 60px;
}
.stages__list {
  margin-bottom: 20px;
}
.stages__item {
  padding-top: 35px;
  margin-top: 71px;
  position: relative;
}
.stages__item:first-child {
  margin-top: 0;
}
.stages__item:nth-child(1)::after, .stages__item:nth-child(2)::after, .stages__item:nth-child(3)::after {
  content: "";
  position: absolute;
  display: block;
  width: 200px;
  height: 100px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  z-index: -1;
}
.stages__item:nth-child(1)::after {
  width: 208px;
  height: 108px;
  background-image: url("../img/arrow-path1.svg");
  left: 55px;
  top: 45px;
}
.stages__item:nth-child(2)::after {
  width: 203px;
  height: 124px;
  background-image: url("../img/arrow-path2.svg");
  right: 65px;
  top: 65px;
}
.stages__item:nth-child(3)::after {
  width: 233px;
  height: 120px;
  background-image: url("../img/arrow-path3.svg");
  left: 25px;
  top: 55px;
}
.stages__item-title {
  position: absolute;
  margin: 0;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 61px;
  letter-spacing: 1px;
  color: #323232;
  z-index: -1;
  top: 0;
  left: 0;
}
.stages__item-title:before {
  content: "";
  position: absolute;
  width: 58px;
  height: 58px;
  background-color: #fff;
  border-radius: 50%;
  z-index: -1;
  top: -13px;
}
.stages__item:nth-child(even) .stages__item-title {
  right: 0;
}
.stages__item:nth-child(even) .stages__item-title:before {
  right: 0;
}
.stages__item-text {
  margin: 0;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  color: #dddddd;
}
.stages__item:nth-child(even) {
  text-align: right;
}
.stages__item:nth-child(even) .stages__item-text {
  text-align: right;
}

.team {
  margin-bottom: 45px;
  padding-left: 20px;
}

.tabs {
  margin-bottom: 24px;
}
.tabs__btn {
  padding: 10px;
  display: inline-block;
  line-height: 14px;
  cursor: pointer;
}
.tabs__btn:hover {
  background-color: #fff;
  color: #000;
}
.tabs__input {
  display: none;
}

.tab {
  display: none;
  margin-top: 24px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom right;
  background-image: url("../img/director.png");
}
.tab:nth-of-type(2) {
  background-image: url("../img/designer.png");
}
.tab:nth-of-type(3) {
  background-image: url("../img/front.png");
}
.tab:nth-of-type(4) {
  background-image: url("../img/back.png");
}
.tab__title {
  margin: 0;
  margin-bottom: 8px;
  font-family: "Floripa";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
}
.tab__subtitle {
  margin: 0;
  margin-bottom: 28px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  color: #949494;
}
.tab__text {
  width: 173px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  color: #dddddd;
}

.tabs > input[type=radio]:checked + label {
  background-color: #fff;
  color: #000;
}

#tab-btn-1:checked ~ #content-1,
#tab-btn-2:checked ~ #content-2,
#tab-btn-3:checked ~ #content-3,
#tab-btn-4:checked ~ #content-4 {
  display: block;
}

.page-footer {
  padding: 0 20px;
  font-family: "Gropled";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
}
.page-footer__link {
  display: block;
  margin-bottom: 12px;
}